@import '@styles/glob.scss';

.feature {
  width: 280px;
  border-bottom: 1px solid rgba(31, 32, 65, 0.1);
  display: grid;
  grid-template-rows: 21px 42px;
  grid-template-columns: 56px 205px;
  grid-template-areas:
    "post-1 post-2"
    "post-1 post-3";
  &__img {
    padding-left: 3px;
    grid-area: post-1;
  }

  &__name {
    grid-area: post-2;
    font-family: "Montserrat", Arial, Helvetica, sans-serif;
    font-weight: bold;
    color: $colorDarkShade75;
    font-size: 14px;
  }

  &__descr {
    grid-area: post-3;
    @include font-body;
    font-size: 14px;
  }
}
