@import '@styles/glob.scss';

.checklist {
  * {
    box-sizing: border-box;
  }

  user-select: none;
  width: 266px;
  &__label {
    @include font-h3;
    position: relative;
    &_collapsing {
      cursor: pointer;
    }
  }
  &__img {
    position: absolute;
    right: 15px;
    top: 3px;
    &-expanded {
      transform: rotate(180deg);
      transition: all 0.3s ease 0s;
    }

    &_collapsed {
      transform: rotate(0deg);
      transition: all 0.3s ease 0s;
    }
    &_non-collapsing {
      display: none;
    }
  }

  &__list-wrapper {
    width: 266px;
    background-color: $colorWhite;
    position: absolute;
    z-index: 2;
    padding-top: 14px;
    &_hidden {
      display: none;
    }
  }
  &__cat-wrapper {
    margin-bottom: 7px;
  }

  &__cat {
    display: inline-flex;
    position: relative;
  }
  &__cat-text {
    margin-left: 9px;
  }

  &__cat-checkbox {
    position: absolute;
    clip: rect(0 0 0 0);
    width: 1px;
    height: 1px;
    margin: -1px;

 
    &:focus + .checklist__cat-checkmark {
      border: 1px solid $colorPurple;
      }

    &:checked + .checklist__cat-checkmark,
    &:checked:focus + .checklist__cat-checkmark {
      border: 1px solid $colorPurple;
      background-image: url("@com/checklist/img/tip.svg");
      background-position-y: 42%;
      background-position-x: 40%;
      background-repeat: no-repeat;
    }
  }

  &__cat-checkmark {
    cursor: pointer;
    display: inline-block;
    position: relative;
    border: 1px solid $colorDarkShade25;
    width: 18px;
    height: 18px;
    border-radius: 5px;
    display: inline-block;
    width: 18px;
    height: 18px;
    background-color: $colorWhite;
  }

  &__cat-name {
    cursor: pointer;
    @include font-body;
    line-height: 19px;
    padding-top: 2px;
    max-width: 215px;
    &_rich {
      font-family: "Montserrat", Arial, Helvetica, sans-serif;
      font-weight: bold;
      margin-bottom: 5px;
    }
  }
  &__cat-descr {
    max-width: 230px;
    cursor: pointer;
    font-family: "Montserrat", Arial, Helvetica, sans-serif;
    font-weight: normal;
    font-size: 12px;
    line-height: 14px;
    color: $colorDarkShade75;
    padding-bottom: 2px;
  }
}
