@import '@styles/glob.scss';

.index {
  &__main {
    background-color: $colorDarkShade5;
  }
  &__header {
    padding-bottom: 20px;
    padding-bottom: 20px;
    font-family: "Montserrat", Arial, Helvetica, sans-serif;
    color: $colorDarkShade75;
  }
  &__pages {
    max-width: $lg;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
  }
  &__link {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: "Montserrat", Arial, Helvetica, sans-serif;
    font-weight: bold;
    font-size: 1rem;
    color: $colorDarkShade75;
    &:hover,
    &:visited,
    &:focus {
      color: $colorDarkShade50;
    }
  }
  &__image {
    margin-bottom: 10px;
    width: 200px;
    height: 250px;
    &:hover {
      opacity: 0.7;
    }
  }
  &__page {
    max-width: 200px;
    display: flex;
    flex-direction: column;
    margin: 20px;
  }
}
